.home {
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 1400px;
  margin: 0 auto;
  padding: 1.5rem 2rem;
  background-color: #fcecd6;
  border-bottom: 2px solid #e2c7a0;
  font-family: 'Lora', sans-serif;
  color: #8b4513;
}

.button {
  margin-top: 1rem;
  padding: 0.75rem 1.5rem;
  background-color: #8b4513;
  color: #fff;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
}


.button:hover {
  background-color: #6f3410;
}
.content {
  font-size: 1.2rem;
  line-height: 1.6;
  max-width: 900px;
  text-align: center;
}

h1 {
  font-size: 1.8rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
}

p {
  margin-bottom: 0.5rem;
}
